<template>
  <div class="main">
    <div class="cube-slide-w">
      <cube-slide ref="slide"
                  :data="pageArr"
                  @change="changePage"
                  @scroll="scrollPage"
                  :show-dots="showDots"
                  :loop="false"
                  :initial-index="currentPage"
                  :auto-play="false">
        <cube-slide-item class="w-slide">
          <div class="item-w">
            <v-home v-if="currentPage === 0"></v-home>
          </div>
        </cube-slide-item>
        <cube-slide-item class="w-slide">
          <div class="item-w">
            <v-find v-if="currentPage === 1"></v-find>
          </div>
        </cube-slide-item>
        <cube-slide-item class="w-slide">
          <div class="item-w">
            <v-order-center v-if="currentPage === 2"></v-order-center>
          </div>
        </cube-slide-item>
        <cube-slide-item class="w-slide">
          <div class="item-w">
            <v-my v-if="currentPage === 3"></v-my>
          </div>
        </cube-slide-item>
      </cube-slide>
    </div>
    <cube-tab-bar v-model="selectedItem"
                  show-slider
                  :use-transition="disabled"
                  :data="pageArr"
                  @click="clickFooter"
                  class="foot-nav"
                  ref="tabNav2">
      <div class="footer-tab-item" label="" @click="changeTab(0)" :class="{active: currentPage === 0}">
        <svg class="icon" v-if="currentPage === 0">
          <use xlink:href="#h5-component-index">
            <svg viewBox="0 0 22 22" id="h5-component-index" width="20" height="20">
              <path fill="#0089dc" fill-rule="evenodd" d="M16.822 17.089l.456.707c.212.33.14.778-.174 1.033a9.91 9.91 0 0 1-.767.555 9.817 9.817 0 0 1-7.473 1.395 9.867 9.867 0 0 1-6.265-4.334C-.383 11.822.927 5.618 5.52 2.616a9.81 9.81 0 0 1 7.475-1.394 9.866 9.866 0 0 1 6.264 4.334c.166.258.323.528.466.803.19.385.072.82-.262 1.039l-9.05 5.916a.783.783 0 0 1-1.086-.232l-.47-.73a1.668 1.668 0 0 1 .484-2.295l5.766-3.769a.286.286 0 0 0 .03-.455 6.576 6.576 0 0 0-7.821-.434 6.636 6.636 0 0 0-2.877 4.213 6.671 6.671 0 0 0 .926 5.026c1.99 3.085 6.104 3.968 9.17 1.969a1.65 1.65 0 0 1 2.288.482zm3.878-5.445c.56.863.314 2.02-.549 2.58l-.906.59a.786.786 0 0 1-1.086-.232l-1.177-1.812a.787.787 0 0 1 .23-1.086l1.813-1.176a.783.783 0 0 1 1.086.23l.589.906z"></path>
            </svg>
          </use>
        </svg>
        <svg class="icon" v-else>
          <use xlink:href="#h5-component-indexRegular">
            <svg viewBox="0 0 22 22" id="h5-component-indexRegular" width="100%" height="100%">
              <path fill="#818181" fill-rule="nonzero" d="M7.924 6.273A5.597 5.597 0 0 0 5.48 9.828a5.596 5.596 0 0 0 .787 4.242 5.646 5.646 0 0 0 7.793 1.66 2.188 2.188 0 0 1 3.02.638l.43.663c.377.58.247 1.358-.3 1.798a10 10 0 0 1-.771.555 9.93 9.93 0 0 1-7.523 1.395 9.937 9.937 0 0 1-6.306-4.334C-.393 11.82.926 5.618 5.55 2.615a9.935 9.935 0 0 1 7.523-1.393 9.937 9.937 0 0 1 6.781 5.148c.318.64.12 1.396-.467 1.777l-8.54 5.546c-.632.41-1.478.23-1.89-.401l-.443-.684a2.182 2.182 0 0 1 .641-3.016l5.011-3.255a5.612 5.612 0 0 0-6.242-.064zm6.813 10.507c-3.184 2.062-7.453 1.152-9.519-2.03a6.846 6.846 0 0 1-.96-5.182 6.847 6.847 0 0 1 2.986-4.344 6.869 6.869 0 0 1 8.13.46.892.892 0 0 1-.098 1.422l-5.44 3.534a.932.932 0 0 0-.274 1.287l.444.684a.117.117 0 0 0 .16.034l8.54-5.547c.05-.032.067-.095.035-.16a8.687 8.687 0 0 0-5.928-4.494 8.685 8.685 0 0 0-6.583 1.22c-4.044 2.627-5.198 8.056-2.572 12.1a8.686 8.686 0 0 0 5.517 3.792 8.68 8.68 0 0 0 6.583-1.22c.231-.15.458-.314.672-.483.047-.038.057-.102.032-.142l-.43-.662a.938.938 0 0 0-1.295-.269zm5.88-5.517c.714 1.099.4 2.571-.697 3.284l-.851.553a1.362 1.362 0 0 1-1.882-.401l-1.103-1.7a1.362 1.362 0 0 1 .4-1.882l1.699-1.102a1.357 1.357 0 0 1 1.883.399l.552.85zm-1.6-.168a.107.107 0 0 0-.07-.048.107.107 0 0 0-.083.016l-1.699 1.102a.112.112 0 0 0-.032.154l1.102 1.698c.021.032.056.05.095.05a.108.108 0 0 0 .06-.016l.849-.552c.519-.337.667-1.035.33-1.555l-.551-.849z"></path>
            </svg>
          </use>
        </svg>
        <p class="item-text">首页</p>
      </div>
      <div class="footer-tab-item" label="" @click="changeTab(1)" :class="{active: currentPage === 1}">
        <svg class="icon" v-if="currentPage === 1">
          <use xlink:href="#h5-component-discover">
            <svg viewBox="0 0 22 22" id="h5-component-discover" width="100%" height="100%">
              <path fill="#0089dc" fill-rule="evenodd" d="M3.929 3.929c3.899-3.9 10.243-3.9 14.142 0 3.899 3.899 3.899 10.243 0 14.142-3.899 3.9-10.243 3.9-14.142 0-3.9-3.899-3.9-10.243 0-14.142zM14.639 7a.363.363 0 0 0-.146.03l-4.39 1.901c-.254.11-.493.264-.701.471a2.23 2.23 0 0 0-.476.712l-1.896 4.38a.363.363 0 0 0 .476.476l4.38-1.896a2.203 2.203 0 0 0 1.183-1.178l1.9-4.39A.363.363 0 0 0 14.64 7zM11 12a1 1 0 1 1 0-2 1 1 0 0 1 0 2z"></path>
            </svg>
          </use>
        </svg>
        <svg class="icon" v-else>
          <use xlink:href="#h5-component-discoverRegular">
            <svg viewBox="0 0 22 22" id="h5-component-discoverRegular" width="100%" height="100%">
              <path fill="#818181" fill-rule="nonzero" d="M16.62 2.727a.75.75 0 0 1-.844 1.24 8.455 8.455 0 0 0-4.095-1.44 8.5 8.5 0 0 0-9.153 7.792 8.499 8.499 0 0 0 7.79 9.153 8.5 8.5 0 0 0 9.154-7.791 8.46 8.46 0 0 0-1.435-5.449.75.75 0 1 1 1.24-.842 9.96 9.96 0 0 1 1.69 6.411c-.442 5.505-5.264 9.609-10.768 9.166-5.505-.442-9.61-5.263-9.166-10.768C1.475 4.694 6.296.59 11.8 1.033c1.75.14 3.398.727 4.819 1.694zM14.638 7c.244 0 .44.254.331.506l-1.9 4.39c-.11.255-.264.494-.471.702-.21.21-.454.367-.712.476l-4.38 1.895a.363.363 0 0 1-.476-.476l1.895-4.38c.11-.258.266-.5.477-.711.207-.208.447-.362.7-.471l4.391-1.9A.367.367 0 0 1 14.638 7zM12.45 9.55l-1.751.757a.737.737 0 0 0-.237.156.753.753 0 0 0-.156.236l-.758 1.752 1.742-.753a.766.766 0 0 0 .247-.161.733.733 0 0 0 .154-.234l.76-1.754z"></path>
            </svg>
          </use>
        </svg>
        <p class="item-text">发现</p>
      </div>
      <div class="footer-tab-item" label="" @click="changeTab(2)" :class="{active: currentPage === 2}">
        <svg class="icon" v-if="currentPage === 2">
          <use xlink:href="#h5-component-order">
            <svg viewBox="0 0 22 22" id="h5-component-order" width="100%" height="100%">
              <path fill="#0089dc" fill-rule="evenodd" d="M2.75 1.5h16.5a.75.75 0 0 1 .75.75v11.5a6.758 6.758 0 0 1-6.75 6.75H2.75a.75.75 0 0 1-.75-.75V2.25a.75.75 0 0 1 .75-.75zm12 6.5a.75.75 0 0 0 0-1.5h-7.5a.75.75 0 0 0 0 1.5h7.5zm-3.002 5.003a.75.75 0 0 0 0-1.5H7.25a.75.75 0 0 0 0 1.5h4.498z"></path>
            </svg>
          </use>
        </svg>
        <svg class="icon" v-else>
          <use xlink:href="#h5-component-orderRegular">
            <svg viewBox="0 0 22 22" id="h5-component-orderRegular" width="100%" height="100%">
              <path fill="#818181" fill-rule="nonzero" d="M7.25 8a.75.75 0 0 1 0-1.5h7.5a.75.75 0 1 1 0 1.5h-7.5zm0 5.004a.75.75 0 1 1 0-1.5h4.498a.75.75 0 1 1 0 1.5H7.25zM3.5 3v16h9.75c2.9 0 5.25-2.35 5.25-5.25V5.748a.75.75 0 1 1 1.5 0v8.002a6.75 6.75 0 0 1-6.75 6.75H2.75a.75.75 0 0 1-.75-.75V2.25a.75.75 0 0 1 .75-.75h16.5a.75.75 0 1 1 0 1.5H3.5z"></path>
            </svg>
          </use>
        </svg>
        <p class="item-text">订单</p>
      </div>
      <div class="footer-tab-item" label="" @click="changeTab(3)" :class="{active: currentPage === 3}">
        <svg class="icon" v-if="currentPage === 3">
          <use xlink:href="#h5-component-profile">
            <svg viewBox="0 0 22 22" id="h5-component-profile" width="100%" height="100%">
              <path fill="#0089dc" fill-rule="evenodd" d="M11 1c2.757 0 5 2.243 5 5v1c0 2.757-2.243 5-5 5S6 9.757 6 7V6c0-2.757 2.243-5 5-5zm5.967 10.071A6.76 6.76 0 0 1 21 17.251v3a.75.75 0 0 1-.75.75H1.75a.75.75 0 0 1-.75-.75v-3a6.76 6.76 0 0 1 4.033-6.18.992.992 0 0 1 1.135.263A6.476 6.476 0 0 0 11 13.5c1.919 0 3.642-.84 4.832-2.166a.993.993 0 0 1 1.135-.263z"></path>
            </svg>
          </use>
        </svg>
        <svg class="icon" v-else>
          <use xlink:href="#h5-component-profileRegular">
            <svg viewBox="0 0 22 22" id="h5-component-profileRegular" width="100%" height="100%">
              <path fill="#818181" fill-rule="nonzero" d="M10.955 12H7.75a5.25 5.25 0 0 0-5.25 5.25v2.25h17v-2.25a5.244 5.244 0 0 0-2.626-4.548.75.75 0 0 1 .75-1.3l.183.11A6.745 6.745 0 0 1 21 17.25v3a.75.75 0 0 1-.75.75H1.75a.75.75 0 0 1-.75-.75v-3a6.75 6.75 0 0 1 6.436-6.743A4.984 4.984 0 0 1 6 7V6a5 5 0 0 1 10 0v1a5 5 0 0 1-5.045 5zM11 10.5A3.5 3.5 0 0 0 14.5 7V6a3.5 3.5 0 0 0-7 0v1a3.5 3.5 0 0 0 3.5 3.5z"></path>
            </svg>
          </use>
        </svg>
        <p class="item-text">我的</p>
      </div>
    </cube-tab-bar>
  </div>
</template>

<script type="text/ecmascript-6">
import home from '@/page/home/home'
import find from '@/page/find/find'
import orderCenter from '@/page/orderCenter/orderCenter'
import my from '@/page/my/my'
import {findIndex} from '@/assets/js/util'

export default {
  data () {
    return {
      selectedItem: 0,
      disabled: false,
      autoPlay: false,
      showDots: false,
      pageArr: [
        {label: '首页'},
        {label: '发现'},
        {label: '订单'},
        {label: '我的'}
      ]
    }
  },
  methods: {
    scrollPage (pos) {
      console.log(pos)
      const x = Math.abs(pos.x)
      const tabItemWidth = this.$refs.tabNav2.$el.clientWidth
      const slideScrollerWidth = this.$refs.slide.slide.scrollerWidth
      const deltaX = x / slideScrollerWidth * tabItemWidth
      this.$refs.tabNav2.setSliderTransform(deltaX)
    },
    clickFooter (index) {
      console.log(index)
    },
    // tab切换
    changeTab (index) {
      this.selectedItem = index
    },
    // 页面切换
    changePage (current) {
      this.selectedItem = current
    }
  },
  computed: {
    currentPage () {
      let index = 0
      index = findIndex(this.pageArr, (item, num) => num === this.selectedItem)
      return index
    }
  },
  components: {
    vHome: home,
    vFind: find,
    vOrderCenter: orderCenter,
    vMy: my
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.main
  width: 100%
  height: 100%
.cube-slide-w
  position: fixed
  bottom: 0
  left: 0
  right: 0
  top: 0
  width: 100%
  height: 100%
  >>> .cube-slide-group .w-slide
    padding-bottom: 50px
    position: relative
  .item-w
    overflow-y auto
    width: 100%
    height: 100%
  >>> .cube-slide-dots
    height: 50px
    width: 100%
    display flex
    align-items center
    padding: 0
    bottom: 0
    box-shadow 0 -2px 3px rgba(0,0,0,.1)
    font-size: 14px
    .footer-tab-item
      display flex
      align-items center
      justify-content center
      flex-direction column
      background-color: #fff
      margin: 0
      flex 1
      height: 100%
      &.active
        background-color: #fff
        .item-text
          color #0089dc
      .icon
        width: 20px
        height: 20px
        margin-bottom: 5px
.foot-nav
  position: fixed
  bottom: 0
  left: 0
  width: 100%
  box-shadow 0 -2px 3px rgba(0,0,0,.1)
  font-size: 14px
  height: 50px
  >>> .cube-tab-bar-slider
    display none
  .footer-tab-item
    display flex
    align-items center
    justify-content center
    flex-direction column
    background-color: #fff
    margin: 0
    flex 1
    height: 100%
    &.active
      background-color: #fff
      .item-text
        color #0089dc
    .icon
      width: 20px
      height: 20px
      margin-bottom: 5px
</style>



